.slider {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    max-width: 1240px;
    min-width: 380px;
    margin-left: auto;
    margin-right: auto;
    height: 415px;
    border-radius: 25px;
    position: relative;
    overflow: hidden;
    @media screen and (max-width: 425px) {
        max-height: 255px;
      }
    // @media screen and (max-width: 425px) {
    //     max-height: 255px;
    //   }
    img{
        display: flex;
        width: 100%;
        align-items: center;
            @media screen and (max-width: 425px) {
        height: 0;
      }
    }
    .FaChevronLeft {
        position: absolute;
        top: 42%;
        left: 2vw;
        font-size: xxx-large;
        color: $color-2;
        z-index: 10;
        cursor: pointer;
      }
      .FaChevronRight {
        position: absolute;
        top: 42%;
        right: 2vw;
        font-size: xxx-large;
        color: $color-2;
        z-index: 10;
        cursor: pointer;
      }
      .slider-picture {
        object-fit: cover;
        animation: sliderFadeIn .20s ease-in-out;
    }
    .slider-counter {
        position: absolute;
        font-family: $font-1;
        font-size: 18px;
        bottom: 25px;
        left: 0;
        right: 0;
        color: white;
        display: flex;
        justify-content: center;
        font-weight: 700;
    }
}
